import React from 'react'
import './Footer.css'

export default function Footer() {
    return (
        <div id="Footer" className="Footer">
            <div className="leftSide">
                <div className="lUpper">
                    <div className="leftUp">
                        <h1>Company</h1>
                        <a href={''}>Terms and Conditions</a>
                        <a href={''}>Privacy Policy</a>
                        <a href={''}></a>
                    </div>
                    <div className="midUp">
                        <h1>Support</h1>
                        <a href={''}>Shipping</a>
                        <a href={''}>Returns and Refunds</a>
                        <a href={''}>Contact Us</a>
                    </div>
                    <div className="rightUp">
                    </div>
                </div>

                <div className="lLower">
                    <div className="upUpper">
                        <h1>Get Bitten! Sign up for our newsletter!</h1> 
                    </div>
                    <div className="lowUpper">
                        <div className="buttonBox">
                            <form className="signUpForm">
                                <input id="newsletter_email" type="newsletter_email"  placeholder="Enter your email" className="news_input"></input>
                                <button type="submit" className="newsletter_button">OK</button>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
            <div className="rightSide">
                <div className="rUp">
                    <h1>Snache</h1>
                </div>
                <div className="rLow">
                    <div className="socials">
                       <a href="https://www.instagram.com/"><ion-icon name="logo-instagram"></ion-icon></a>
                        <a href="https://www.facebook.com/"><ion-icon name="logo-facebook"></ion-icon></a>
                    </div>
                </div>
                <div className="rBottom">
                    <div className="copyright">
                        <p>Adam Silc &copy;2021</p>
                    </div>
                </div>
            </div>
        </div>
    )
}
