import React from 'react'
import './Products.css'

export default function Products() {
    return (
        <div id="Products" className="Products">
            <div className="leftHero">
                <div className="lToggle"></div>
                <div className="leCard">
                    <div className="leTop">
                        <h1>Fully restores health<br></br> and fortifies slightly</h1>
                    </div>
                    <div className="leMid">
                        <h1>Snache</h1>
                        <p>"The Original"</p>
                        <p>Snake Oil</p>
                    </div>
                    <div className="leBot">
                        <h1>100 DROPS</h1>
                        <p>100 x 1.5 ML</p>
                        <p>Herbal Supplement</p>
                    </div>
                </div>
                <div className="health">
                    <span>H</span>
                    <span>E</span>
                    <span>A</span>
                    <span>L</span>
                    <span>T</span>
                    <span>H</span>
                </div>
            </div>
            <div className="rightHero">
                <div className="rToggle"></div>
                <div className="riCard">
                    <div className="riTop">
                        <h1>Fully restores stamina<br></br> and fortifies moderately</h1>
                    </div>
                    <div className="riMid">
                        <h1>Snache</h1>
                        <p>"NEW!"</p>
                        <p>Special Snake Oil</p>
                    </div>
                    <div className="riBot">
                        <h1>60 GEL PILLS</h1>
                        <p>60 x 20 MG</p>
                        <p>Herbal Supplement</p>
                    </div>
                </div>
                <div className="stamina">
                    <span>S</span>
                    <span>T</span>
                    <span>A</span>
                    <span>M</span>
                    <span>I</span>
                    <span>N</span>
                    <span>A</span>
                </div>
            </div>
        </div>
    )
}
